.theme-toggle-button {
  position: relative;
  display: flex;
  align-items: center;
}
.theme-toggle-button .light-theme-icon > span, .theme-toggle-button .dark-theme-icon > span {
  width: 18px;
  color: var(--pgn-color-primary-base);
}
.theme-toggle-button .toggle-switch {
  display: flex;
  margin: 0 2px;
  /* The switch - the box around the slider */
}
.theme-toggle-button .toggle-switch label {
  margin: 0;
}
.theme-toggle-button .toggle-switch .switch {
  position: relative;
  display: inline-block;
  width: 45px;
  height: 24px;
  /* The slider */
}
.theme-toggle-button .toggle-switch .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.theme-toggle-button .toggle-switch .switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #AEC7F6;
  transition: 0.4s;
}
.theme-toggle-button .toggle-switch .switch .slider::before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 3px;
  bottom: 2px;
  background-color: white;
  transition: 0.4s;
}
.theme-toggle-button .toggle-switch .switch .slider.round {
  border-radius: 34px;
}
.theme-toggle-button .toggle-switch .switch .slider.round::before {
  border-radius: 50%;
}